@import url("https://fonts.googleapis.com/css?family=Acme&display=swap");

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

#App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

/* Home */

#form {
  margin-top: 120px;
}
#search-bar {
  width: 430px;
  height: 40px;
  border-radius: 10px;
  outline: none;
  padding: 10px;
  box-sizing: border-box;
  transition: 0.3s;
}

#search-bar:focus {
  border-color: dodgerblue;
  box-shadow: 0 0 8px 0 dodgerblue;
}

#submit-button {
  border-radius: 30px;
}
#submit-button:hover {
  background-color: #282c34;
}
#submit-button:focus {
  outline: none;
}

@media screen and (max-width: 550px) {
  #submit-button {
    margin-top: 30px;
  }
}

#song-list{
  margin-top : 100px;
  margin-left : 50px;
  margin-right : 50px;
}

#song-list ul{
  list-style: none;
}

.song{
  background-color : rgba(218, 218, 218, 0.582);
  margin-top : 70px;
  padding :  0 20px;
  position: relative;
  
}
.song-title{
  padding-top : 20px;
  text-align: center;
  overflow: scroll;
}

.song-img{
  width : 60%;
  height : 20%;
  padding : 30px;
  margin : auto auto;
}

.song-btn{ 
  position : absolute;
  /* width : 10%;
  height : 8%; */
  text-align : center;
  bottom : 0;
  right : 0;
  padding-top : 10px;
  overflow-wrap: break-word;
  overflow: auto;
  
}

@media screen and (max-width : 650px){
  .song{
    padding-bottom : 50px;
  }
}

/* Analysis */

#analysis-page {
  text-align: center;
  position : relative;
}

#loading{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size : 10rem;
}

#analysis-page ul {
  list-style: none;
}
